:root {
    --color-gray-light-1: #f8f8f8;
    --color-gray-dark-1: #545454;
    --color-tiffany: #00a6a6;
    --profile-theme: #048f1b;
    --timeline-circle-theme: #14253e;
    
    --background-color-light: #fff;
    --text-color-light: #000;
    --pgbar-length: 100%;
    --background-color-dark: #000;
    --text-color-dark: #fff;
    --MATH-PI: 3.1415px;
	--percent: 100;
}

.App-link {
    color: var(--link_color);
}



.category {
    display: flex;
    align-items: center;
    gap: 10px;
}

.progress-bar {
    display: flex;
    gap: 5px;
}

#span1 {
    padding-right: 10px;
}

.filled,
.empty {
    width: 10px;
    height: 10px;
    border-radius: 50%;
}

.filled {
    background-color: green;
}

.empty {
    background-color: #E0E0E0;
}

html {
    font-size: 100%;
    font-family: 'PT Sans', sans-serif;


}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 3rem 1rem;
    min-height: 100vh;
    background-color: var(--body_background);
    color: var(--text-color-light);
    font-size: 100%;
    font-family: 'PT Sans', sans-serif;
    box-sizing: border-box;
    transition: background-color 0.3s, color 0.3s;
}

.wrapper {
    margin: 0 auto;
    max-width: 70rem;
    display: flex;
    box-shadow: 0px 0px 15px 4px #b3b3b3;
    background-color: var(--body_background);
    color: var(--body_color);
}

.intro {
    flex: 0 0 250px;
    background-color: var(--body_background);
    color: var(--body_color);
    box-shadow: 5px 0px 15px 0px #b3b3b3;
    z-index: 5;
}

.profile {
    position: relative;
    background-color: var(--profile-theme);
    padding: 2rem 1rem;
    margin-bottom: 50px;
    text-align: center;
}

.profile::after {
    content: " ";
    position: absolute;
    left: 0;
    bottom: -15px;
    width: 100%;
    height: 30px;
    background-color: var(--body_background);
    color: var(--body_color);
    transform: skewY(-5deg);
}

.photo img {
    width: 80%;
    border-radius: 50%;
}

.bio .name {
    font-size: 1.5rem;
    text-align: center;
    color: var(--body_color);
    margin: 0;
    margin-top: 1rem;
}

.bio .profession {
    font-size: 1rem;
    text-align: center;
    margin: 0;
}

.intro-section {
    padding: 0 1rem;
    color: var(--body_color);
    background-color: var(--body_background);
}

.intro-section .title {
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: var(--body_color);
    background-color: var(--body_background);
}

.about .paragraph {
    text-align: justify;
}

.info-section {
    margin: 1rem 0;
}

.info-section span {
    position: relative;
    transition: all 0.3s;
}

.info-section i {
    color: var(--body_color);
    background-color: var(--body_background);
    width: 20px;
    height: 20px;
}

.link a {
    text-decoration: none;
    color: inherit;
}

.link span::after {
    position: absolute;
    content: '';
    left: 50%;
    bottom: -3px;
    width: 0;
    height: 2px;
    background-color: var(--body_background);
    color: var(--body_color);
    transition: width 0.3s;
    transition-timing-function: cubic-bezier(.92, .57, .33, 1.02);
    transform: translate(-50%, 0);
}

.link span:hover {
    color: var(--body_color);
    background-color: var(--body_background);
}

.link span:hover::after {
    width: 100%;
}

.detail {
    flex: 1 0 0;
    padding: 2rem;
    background-color: var(--body_background);
    color: var(--body_color);
}

.detail:hover>.detail-section:not(:hover) {
    opacity: 0.3;
}

.detail-section {
    transition: all 0.3s;
}

.detail-section:not(:last-of-type) {
    padding-bottom: 1rem;
}

.detail-title {
    display: flex;
    align-items: center;
}

.detail-section>.detail-content {
    padding: 1.5rem;
    padding-left: 2rem;
}

.detail-section.edu>.detail-content {
    padding-left: calc(1.5rem + 10px);
}

.title-icon+span {
    font-size: 1.5rem;
    transition: all 0.3s;
}

.title-icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    margin-right: 1rem;
    width: 2.5rem;
    height: 2.5rem;
    background-color: var(--body_background);
    color: var(--body_color);
    border-radius: 50%;
    transition: all 0.3s;
}

.title-icon i {
    color: white;
    line-height: 1rem;
    font-size: 1rem;
    text-align: center;
}

.detail-section:hover .title-icon {
    transform: scale(1.5);
    margin-right: 1.5rem;
}

.detail-section:hover .title-icon+span {
    letter-spacing: 1.5px;
}

.timeline-block {
    position: relative;
    padding-left: 30px;
}

.timeline-block:not(:last-of-type) {
    margin-bottom: 1rem;
}

.timeline-block h1 {
    font-size: 1rem;
    margin: 5px 0;
    transition: all 0.3s;
}

.timeline-block p {
    font-size: 0.8rem;
    margin: 5px 0;
}

.timeline-block time {
    font-size: 0.8rem;
    color: var(--color-gray-dark-2);
}

.timeline-block::before {
    position: absolute;
    content: '';
    width: 15px;
    height: 15px;
    background-color: white;
    border: 3px solid var(--timeline-circle-theme);
    border-radius: 50%;
    left: -10px;
    top: -5px;
}

.timeline-block::after {
    position: absolute;
    content: '';
    width: 3px;
    height: 100%;
    background-color: var(--timeline-circle-theme);
    left: -1px;
    top: 13px;
}

.timeline-block:hover h1 {
    color: var(--color-gray-dark-1);
}

.timeline-block:hover::before {
    animation: circle 1.2s infinite;
}

.pg-list, .tool-list, .favor-list {
	padding: 0;
	list-style: none;
}
.pg-list > li {	
	margin: 1rem 0;
	display: flex;
	align-items: center;
}
.sb-skeleton {
	position: relative;
	flex: 1 0 auto;
	height: 2px;
	background-color: var(--color-gray-dark-1);
}
.pg-list > li > span {
	flex: 0 0 100px;
}
.sb-skeleton > .skillbar {
	position: absolute;
	left: 0;
	top: -1px;
	width: var(--pgbar-length);
	height: 4px;
	background-color: var(--profile-theme);
}

.tool-list {
    list-style: none;
    display: flex;
    justify-content: space-between;
}

.tool-list>li {
    position: relative;
    text-align: center;
    flex: 0 0 25%;
}

.tool-list>li>svg {
    position: relative;
    fill: transparent;
    width: 95%;
    transform: rotate(-90deg);
}

.tool-list>li>svg>circle {
    stroke-width: 1px;
    stroke: #cdcdcd;
}

.tool-list>li>svg>circle.cbar {
    stroke-width: 3px;
    stroke: var(--profile-theme);
    stroke-linecap: round;
    stroke-dashoffset: 0;
    stroke-dasharray: calc(var(--MATH-PI) * 45 * 2);
    transition: all 0.8s;
    transition-timing-function: cubic-bezier(.64, .51, .16, .86);
}

.tool-list>li:hover>svg>circle.cbar {
    stroke-width: 4px;
    stroke: var(--color-tiffany);
    stroke-dashoffset: calc(var(--MATH-PI) * 45 * 2 * (1 - var(--percent)));
}

.tool-list>li>.tl-name,
.tool-list>li>.tl-exp {
    position: absolute;
    left: 50%;
    color: var(--body_color);
}

.tool-list>li>.tl-name {
    top: 50%;
    font-size: 1.2rem;
    transform: translate(-50%, -50%);
}

.tool-list>li>.tl-exp {
    top: calc(50% + 1.4rem);
    font-size: 1rem;
    transform: translate(-50%, -50%);
}

.outer-frame {
    border: 1px solid var(--color-gray-dark-3);
    border-radius: 5px;
}


@keyframes circle {
    0% {
        box-shadow: 0 0 0 0px rgba(51, 52, 57, 1);
    }

    100% {
        box-shadow: 0 0 0 6px rgba(51, 52, 57, 0);
    }
}

@media (max-width: 1024px) {
    html {
        font-size: 90%;
    }

    .wrapper {
        flex-direction: column;
    }

    .intro,
    .detail {
        flex: 0 0 100%;
        box-shadow: none;

    }

    .intro-section {
        padding: 0 2rem;
    }

    .profile {
        margin-bottom: 1rem;
    }

    .profile::after {
        display: none;
    }

    .photo img {
        width: 100px;
        height: 100px;
    }
}

@media (max-width: 768px) {
    html {
        font-size: 80%;
    }

    .tool-list {
        flex-wrap: wrap;
    }

    .tool-list>li {
        flex: 0 0 33.33%;
    }

    .favor-list>li {
        flex: 0 0 33.3333%;
    }
}

@media (max-width: 500px) {
    html {
        font-size: 70%;
    }
}

.dark_mode_label {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.dark_mode_label input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: gray;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: black;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked+.slider {
    background-color: gray;
}

input:focus+.slider {
    box-shadow: 0 0 1px black;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}